*,
*::after,
*::before {
  box-sizing: border-box;

}

html,
body {
  /* height: 100%; */
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  
}

.main {
  flex: 1 1 auto;

}

.container {
  margin: 0 auto;
}

body{
  font-family: 'Poppins', sans-serif;
  background: var(--pp3-background, linear-gradient(113deg, #984D38 -30.3%, #181E41 58.12%)) no-repeat;
  font-weight: 500;
  color: #fff;
}


