@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap");
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  font-size: inherit;
}

body {
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input,
select {
  vertical-align: middle;
}

ul {
  list-style: none;
}

/**
 * Сброс стилей у кнопки.
 * Придётся немного поработать, чтобы получить нейтральный вид.
*/
button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  /* отображаем курсор в виде руки при наведении; некоторые
  считают, что необходимо оставлять стрелочный вид для кнопок */
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

h1 {
  font-weight: 400;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  /* height: 100%; */
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}

.container {
  margin: 0 auto;
}

body {
  font-family: "Poppins", sans-serif;
  background: var(--pp3-background, linear-gradient(113deg, #984D38 -30.3%, #181E41 58.12%)) no-repeat;
  font-weight: 500;
  color: #fff;
}

.header {
  max-width: 1197px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 110px;
  padding: 20px 15px 110px 15px;
}

.header__logo {
  display: block;
  width: 131px;
  height: 25px;
  background-image: url(../img/logo1.svg);
  background-repeat: no-repeat;
}

.form {
  position: relative;
}

.form__button {
  display: block;
  width: 20px;
  height: 25px;
  background-image: url(../img/search.svg);
  background-repeat: no-repeat;
  background-color: transparent;
  border: none;
  position: absolute;
  top: calc(50% - 10px);
  left: 10px;
}

.form__input:focus-visible {
  border: none;
}

.form__input {
  width: 384px;
  border-radius: 10px;
  background: var(--white10, rgba(255, 255, 255, 0.1));
  padding: 10px 40px;
  border: none;
  color: #fff;
  font-size: 20px;
}

.form__input::placeholder {
  color: var(--half-white, rgba(255, 255, 255, 0.5));
}

.form__input:focus {
  outline: none;
}

.header__list {
  display: flex;
  align-items: center;
  gap: 40px;
  font-size: 20px;
}

.header__link {
  color: #fff;
}

.header__link_gradient {
  border-radius: 10px;
  background: var(--pp2-purple, linear-gradient(214deg, #B75CFF 6.04%, #671AE4 92.95%));
  padding: 10px 20px;
  display: block;
}

.container {
  max-width: 1197px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.main {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main__text {
  flex-basis: 490px;
}

.main__title {
  font-size: 50px;
  font-weight: 700;
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 10px;
}

.main__desc {
  font-size: 20px;
  line-height: 1.8;
  color: rgba(255, 255, 255, 0.5333333333);
  margin-top: 0;
  margin-bottom: 42px;
}

.main__btn {
  display: inline-block;
  font-size: 20px;
  color: #fff;
  border-radius: 10px;
  background: var(--white10, rgba(255, 255, 255, 0.1));
  padding: 11px 20px;
}

.main__btn_gradient {
  background: var(--pp2-purple, linear-gradient(214deg, #B75CFF 6.04%, #671AE4 92.95%));
  margin-right: 17px;
}

.main__image {
  flex-basis: calc(100% - 490px - 116px);
}

.main__img {
  width: 100%;
}

.main__list {
  display: flex;
  text-align: center;
  gap: 110px;
  margin-top: 60px;
  margin-bottom: 24px;
}

.main__item {
  color: rgba(255, 255, 255, 0.5);
  font-size: 20px;
}

.main__span {
  display: block;
  color: #fff;
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 8px;
}

.company {
  display: flex;
  justify-content: space-between;
  margin-top: 150px;
  margin-bottom: 200px;
}

.title {
  line-height: 1.8;
  font-size: 40px;
  font-weight: 700;
  text-align: center;
  max-width: 390px;
  margin: 0 auto 4px auto;
}

.desc {
  font-size: 20px;
  color: rgba(255, 255, 255, 0.5);
  text-align: center;
  max-width: 432px;
  margin: 0 auto 30px auto;
}

.card {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.card__item {
  border-radius: 10px;
  background: var(--white10, rgba(255, 255, 255, 0.1));
  box-shadow: 0px 5px 4px 0px rgba(0, 0, 0, 0.25);
  padding: 26px;
  flex-basis: 32.5%;
  margin-bottom: 200px;
  transition: all 0.3s linear;
}

.card__item:hover {
  background: var(--white10, rgba(255, 255, 255, 0.5));
}

.card__cover {
  position: relative;
  overflow: hidden;
  width: 100%;
  border-radius: 10px;
  height: 380px;
}

.card__img {
  width: 100%;
  height: 380px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}

.card__info {
  display: flex;
  justify-content: space-between;
  margin-top: 24px;
}
.card__nick {
  font-size: 20px;
  color: rgba(255, 255, 255, 0.5);
}
.card__author {
  font-size: 20px;
  font-weight: 700;
  margin-top: 16px;
}
.card__bid {
  text-align: right;
  font-size: 15px;
  color: rgba(255, 255, 255, 0.5);
}
.card__price {
  display: block;
  font-size: 20px;
  color: #fff;
  font-weight: 700;
  margin-top: 5px;
}
.card__link {
  display: block;
  text-align: center;
  font-size: 20px;
  color: #fff;
  border-radius: 10px;
  background: var(--pp2-purple, linear-gradient(214deg, #B75CFF 6.04%, #671AE4 92.95%));
  padding: 14px 20px;
  margin-top: 26px;
}
.card__link:hover {
  background: linear-gradient(45deg, #B75cff 33%, #671AE4 60%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  border: 2px solid var(--pp2-purple, #B75CFF);
}

.collapsible {
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  border-bottom: 1px solid #fff;
  text-align: left;
  outline: none;
  font-size: 20px;
  margin-bottom: 32px;
}

.active,
.collapsible:hover:after,
.collapsible:hover {
  color: #fff;
}

.collapsible:after {
  content: "+";
  color: rgba(255, 255, 255, 0.5);
  float: right;
  margin-left: 5px;
  margin-right: 7px;
}

.active:after {
  content: "−";
  color: white;
}

.content {
  padding: 0 18px;
  max-height: 0;
  overflow: hidden;
  line-height: 1.5;
  transition: max-height 0.2s ease-out;
}

.faq {
  display: flex;
  justify-content: space-between;
  margin-top: 46px;
}

.faq__item {
  flex-basis: 49.3%;
}

.banner {
  border-radius: 10px;
  background: var(--pp2-purple, linear-gradient(214deg, #B75CFF 6.04%, #671AE4 92.95%));
  padding: 51px 15px 64px;
  margin-top: 118px;
  margin-bottom: 100px;
  text-align: center;
}

.banner__title {
  font-size: 50px;
  font-weight: 700;
  max-width: 500px;
  line-height: normal;
  margin: 0 auto 32px;
}

.banner__link {
  border-radius: 10px;
  background: var(--half-white, rgba(255, 255, 255, 0.5));
  padding: 11px 20px;
  margin: 20px auto 0 auto;
  max-width: 155px;
}

.header__buttons {
  display: none;
}

@media (max-width: 1130px) {
  .header,
.container {
    max-width: 900px;
  }

  .header__list {
    gap: 20px;
    font-size: 16px;
  }

  .form__input {
    width: 260px;
    padding: 13px 13px 13px 40px;
    font-size: 14px;
  }

  .main__text {
    flex-basis: 450px;
  }

  .main__title {
    font-size: 43px;
  }

  .main__desc,
.main__item,
.main__btn,
.desc,
.card__link,
.collapsible {
    font-size: 18px;
  }

  .main__image {
    flex-basis: calc(100% - 450px - 30px);
  }

  .main__span {
    font-size: 26px;
  }

  .main__list {
    gap: 40px;
  }

  .company__img {
    height: 52px;
  }

  .company {
    margin-bottom: 150px;
    margin-top: 90px;
  }

  .title {
    font-size: 36px;
  }

  .title {
    font-size: 36px;
  }

  .card__cover,
.card__img {
    height: 300px;
  }

  .card__price,
.card__autor,
.card__nick {
    font-size: 16px;
  }
}
@media (max-width: 880px) {
  .header,
.container {
    max-width: 900px;
  }

  .header__buttons {
    display: flex;
    gap: 22px;
  }

  .form {
    display: none;
  }

  .header__nav {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(48, 13, 105, 0.831372549);
    transform: translateY(-200%);
    transition: all 0.3s linear;
  }

  .header__nav.active {
    transform: translateY(0);
  }
  .header__nav.active::after {
    content: none;
  }

  .header__list {
    flex-direction: column;
    font-size: 20px;
    padding: 20px;
  }

  .header__search,
.header__menu {
    display: block;
    border: none;
    background: transparent;
  }

  .header__menu {
    width: 40px;
    height: 30px;
    position: relative;
    z-index: 2;
  }
  .header__menu span, .header__menu::after, .header__menu::before {
    width: 100%;
    height: 4px;
    position: absolute;
    left: 0;
    background-color: #fff;
  }
  .header__menu span {
    top: 50%;
    transform: translateY(-50%);
    transition: all 0.3s linear;
  }
  .header__menu::after {
    content: "";
    bottom: 0;
    transition: all 0.3s linear;
  }
  .header__menu::before {
    content: "";
    top: 0;
    transition: all 0.3s linear;
  }

  .header__menu.active span {
    opacity: 0;
  }
  .header__menu.active::after {
    content: "";
    bottom: 50%;
    transform: translateY(50%) rotate(315deg);
  }
  .header__menu.active::before {
    content: "";
    top: 50%;
    transform: translateY(-50%) rotate(405deg);
  }

  .header__search {
    background-image: url(../img/search.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 31px;
    height: 30px;
  }

  .header__search.active {
    display: none;
  }

  .form.active {
    display: block;
  }

  .form.active::after {
    display: none;
  }

  .main {
    position: relative;
  }

  .main__image {
    flex-basis: inherit;
    max-width: 33vw;
    position: absolute;
    right: 0;
    bottom: 50%;
    transform: translateY(50%);
  }

  .company__img {
    height: 5.5vw;
  }

  .company {
    margin-top: 55px;
    margin-bottom: 60px;
  }

  .card__item {
    margin-bottom: 60px;
  }

  .card__cover,
.card__img {
    height: 30vw;
  }

  .card__info {
    flex-direction: column;
    text-align: center;
  }

  .card__bid {
    margin-top: 20px;
    text-align: center;
  }

  .card__link {
    margin-top: 15px;
  }

  .card__item {
    padding: 15px;
  }

  .banner {
    padding: 30px 15px 48px;
  }

  .banner__title {
    font-size: 30px;
  }
}
@media (max-width: 600px) {
  .header,
.container {
    max-width: 400px;
  }

  .header {
    padding-bottom: 60px;
  }

  .main__text {
    flex-basis: 100%;
    text-align: center;
  }

  .main__title {
    font-size: 34px;
  }

  .main__list {
    justify-content: center;
  }

  .card__link,
.collapsible,
.desc,
.main__btn,
.main__desc,
.main__item {
    font-size: 16px;
  }

  .desc {
    max-width: 300px;
  }

  .faq,
.card,
.company {
    flex-direction: column;
  }

  .company {
    align-items: center;
    margin-top: 30px;
    margin-bottom: 100px;
  }

  .company__img {
    height: 10vw;
    margin-bottom: 15px;
  }

  .card {
    max-width: 300px;
    margin: 30px auto 100px;
  }

  .card__item {
    margin-bottom: 15px;
  }

  .faq {
    max-width: 400px;
  }

  .main__image {
    display: none;
  }

  .banner__title {
    font-size: 22px;
  }

  .card__cover,
.card__img {
    height: 50vw;
  }

  .form.active .form__input {
    width: 164px;
    padding: 10px 10px 10px 37px;
  }
}
